<template>
  <div class="customInfo" v-if="timerRunning == false">
    <h3>Der Timer ist abgelaufen vielen Dank für deine Teilnahme! Du darfst das Fenster jetzt schließen</h3>
    <!-- <button @click="$router.back()">Zurück zur Startseite</button> -->
  </div>
  <vue-countdown :time="time" v-slot="{  minutes, seconds }" @end="endTimer" @progress="updateTime">
    Time Remaining： {{ minutes }} minutes, {{ seconds }} seconds.
  </vue-countdown>
  <div v-if="timerRunning == true">
    <AufgabenSpiel/>
  </div>
</template>

<script>  
    import AufgabenSpiel from '@/components/AufgabenSpiel.vue'
    import { useApplicationDataStore } from '../stores/applicationDataStore.js'
    import { useCredentialStore } from '../stores/credentialStore.js'
    // import { mapState } from 'pinia'
    import restService from '@/services/restService.js'

export default {
  name: 'AufgabenSpielView',
  components: {
      AufgabenSpiel 
  },
  setup() {
    const appDataStore = useApplicationDataStore()
    const credentialStore = useCredentialStore()
    return { appDataStore, credentialStore}
  },
  data() {
    const timeData = this.appDataStore.time != null ? this.appDataStore.time : 10 * 60 * 1000 ;

    return {
      timerRunning: true,
      time: timeData
    }
  },
  methods: {
    endTimer() {
            this.timerRunning = false
                  //push game end
            const data = JSON.stringify({
                            title: "Probe End",
                            nutzername: this.appDataStore.userName,
                            status: "publish"
            })
            console.log( this.credentialStore.token )
            restService.createTestProbe(data, this.credentialStore.token)

            this.credentialStore.clear()
            this.appDataStore.clear()
    },
    updateTime(data) {
     
      this.appDataStore.time = data.totalMilliseconds
    }
  }
    
  
  };
</script>

<style lang="scss" scoped>
    .customInfo  {
        
            text-align: center;
        
        
    }
</style>