<template>
    <div class="heading">
        <h1>Wilkommen beim Aufgabenspiel</h1>
        
    </div>
    <div>
      <p class="headingText"> {{ this.settingsStore.textStartPage }} </p>
      
    </div>
    <div class="videoSection">
      <p class="videoText" v-html="this.settingsStore.videoText"> </p>
        <video v-if="videoLink" class="video" width="700" height="400" controls>
          <source id="video" :src="videoLink" type="video/mp4">
          Your browser does not support the video tag.
      </video>
    </div>
    <div class="datenschutzCheck">
    <input v-model="checked" type="checkbox" id="datenschutz"><label for="datenschutz">Ich habe die <a :href="this.settingsStore.pdfLink">Datenschutzbestimmungen</a> gelesen und bin damit einverstanden.</label>
    </div>      
 
    <div  class="container">
        <p>Bitte gebe deinen anonymisierten Namen ein:</p>   
        <p v-html="this.settingsStore.textAnonymName"> </p>
        
        <input class ="textinput" :disabled="checked == false" v-model="message" placeholder="write name" />
        <br>
        <button :disabled="checked == false" @click="startApplication">Start</button>
    </div>
</template>

<script>

import { storeToRefs } from 'pinia'
import { useApplicationDataStore } from '../stores/applicationDataStore.js'
import { useCredentialStore } from '@/stores/credentialStore.js'
import { useSettingsStore } from '@/stores/settingsStore.js'
import restService from '@/services/restService.js'

import {  onMounted } from 'vue'

// const el = ref()


onMounted(() => {
  console.log("tst")
  var vid = document.getElementById('video');
  vid.setAttribute('src', this.settingsStore.videoLink);
  console.log("tst")
})

export default { 
  name:"HomePage",
  setup() {
   const settingsStore = useSettingsStore()

    settingsStore.fetchSettings()
    // const { authenticate } = useCredentialStore()      
    const appDataStore = useApplicationDataStore()
    const credentialStore = useCredentialStore()
    const { videoLink } = storeToRefs(useSettingsStore())
    // authenticate()
    credentialStore.authenticate()

    // console.log(settingsStore.videoLink)

    return { appDataStore, credentialStore, settingsStore , videoLink }

  },
  data() {
    return {
      message: '',
      checked: false,
      video: this.videoLink
    }
  },
  computed: {

  },
  methods: {
      startApplication: function () {
        //set data
        this.appDataStore.userName = this.message
     
        this.appDataStore.gamestart = new Date()

        //push game start
        const data = JSON.stringify({
                        title: "Probe Start",
                        nutzername: this.appDataStore.userName,
                        status: "publish"

                })
        console.log( data )
        restService.createTestProbe(data, this.credentialStore.token)

        const router = this.$router;
        router.push({ path: '/aufgaben' });
      }
  },

   
}


</script>

