import { defineStore } from 'pinia'
import axios from 'axios'
import restService from '@/services/restService.js'

function compareTask( a, b ) {
    if ( a.title.rendered < b.title.rendered ){
      return -1;
    }
    if ( a.title.rendered > b.title.rendered ){
      return 1;
    }
    return 0;
  }

export const useTaskStore = defineStore({
    id: 'tasks',
    state: () => ({
      tasks: [],
      task: null,
      checked: true,
      post: null,
      loading: false,
      error: null,
      basePath: "https://testspiel.danielglueck.de/wp-json",
      taskPath: "/wp/v2/aufgabe",
      allposts: "?per_page=100",
      checkpath: "/wp/v2/checkdata?title="
    }),
    getters: {
        getTasks(state) {
            return state.tasks.sort(compareTask)
        },
        getEaysyTasks: (state) =>  {
            return () => state.tasks.filter((task) => task.einfach === "1").sort(compareTask)
        },
        getHardTasks: (state) =>  {
            return () => state.tasks.filter((task) => task.einfach === "0").sort(compareTask)
        },
        getSpecificTask(state) {
            console.log(state.tasks.length)
            return (taskID) => state.tasks.filter((task) => task.id === taskID)
        }
    }, 
    actions: {
        async loadTask(id) {
            this.task = this.getSpecificTask(id)
        },
        async fetchTasks() {
            this.tasks = []
            this.loading = true
        
            this.tasks = await axios.get(this.basePath + this.taskPath + this.allposts).then((response) => {
                console.log(response.data)
                return response.data
            }).catch(
                error => console.log(error)
            ).finally(
                () => { 
                    this.loading = false
                }
            )
        }, 
        
        async fetchCheck(title, username) {
            this.post = null
            this.loading = true
            this.checked = await axios.get(this.basePath + this.checkpath + title + "&u=" + username ).then((response) => {
                console.log(response.data)
                console.log(response.data == "No post to show")
                // this.checked = 
                // console.log(this.checked)
                return response.data == "No post to show"
            }).catch(
                error => console.log(error)
            ).finally(
                () => { 
                    this.loading = false
                }
            )
        },

        async fetchTask(id) {
            this.post = null
            this.loading = true
            this.task = await axios.get(this.basePath + this.taskPath + "/" + id).then((response) => {
                console.log(response.data)
                return response.data
            }).catch(
                error => console.log(error)
            ).finally(
                () => { 
                    this.loading = false
                }
            )
        }, 

        async fetchTaskWithCheck(id, username, now, gamestart, token) {
            this.post = null
            this.loading = true
            await axios.get(this.basePath + this.taskPath + "/" + id).then((response) => {
                console.log(response.data)
                this.task = response.data
                
                

                const data = JSON.stringify({
                    title: "Probe In",
                    aufgabentitel: this.task.title.rendered, 
                    einfach: this.task.einfach, 
                    zp_begonnen: now.toLocaleTimeString(),
                    zp_abgeschlossen: now.toLocaleTimeString(), 
                    zp_spielbeginn: gamestart.toLocaleTimeString(),
                    nutzername: username,
                    status: "publish"
                })
                restService.createTestProbe(data, token)
      

                       
                return response.data
            }).catch(
                error => console.log(error)
            ).finally(
                
                () => { 
                    this.loading = false
                    this.fetchCheck(this.task.title.rendered, username)
                }
            )

        }

    }
  })
  