import { defineStore } from 'pinia'


export const useApplicationDataStore = defineStore({
    id: 'appDataStore',
    state: () => ({
      userName: "",
      time: null,
      gamestart: null
    }),
    getters: {
      getUserName: (state) => {
        return state.userName
      }

    }, 
    actions: {
      async setUserName() {
        this.userName = "abc"
      }, 
      async clear() {
        this.userName = ""
        this.time = null
      }
    }
      
  })