import { defineStore } from 'pinia'
import axios from 'axios'


function compare( a, b ) {
    if ( a.title.rendered < b.title.rendered ){
      return -1;
    }
    if ( a.title.rendered > b.title.rendered ){
      return 1;
    }
    return 0;
  }

export const useQuestionStore = defineStore({
    id: 'questions',
    state: () => ({
      questions: [],
      loading: false,
      error: null,
      basePath: "https://testspiel.danielglueck.de/wp-json",
      taskPath: "/wp/v2/frage"
    }),
    getters: {
        getQuestions(state) {
            return state.questions.sort(compare)
        },
        getEaysyQuestions: (state) =>  {
            return () => state.questions.sort(compare).filter((quest) => quest.einfach === "1")
        },
        getHardQuestions: (state) =>  {
            return () => state.questions.filter((quest) => quest.einfach === "0").sort(compare)
        },
    }, 
    actions: {
     
        async fetchQuestions() {
            this.questions = []
            this.loading = true
        
            this.questions = await axios.get(this.basePath + this.taskPath).then((response) => {
                console.log(response.data)
                return response.data
            }).catch(
                error => console.log(error)
            ).finally(
                () => { 
                    this.loading = false
                }
            )
        }
    }
  })
  