import { defineStore } from 'pinia'
import axios from 'axios'

const configUrlEncoded = {
    headers: { 
      "Content-Type": "application/json"
}}

const credentials = JSON.stringify({
    username: 'glueck',
    password: 'Neopala1994'
})

export const useCredentialStore = defineStore({
    id: 'credentials',
    state: () => ({
      token: "",
      basePath: "https://testspiel.danielglueck.de/wp-json",
      authPath: "/jwt-auth/v1/token/",
      taskPath: "/wp/v2/aufgabe"
    }),
    getters: {
      getToken: (state) => {
        return state.token
      }
    }, 
    actions: {
      async authenticate() {
        axios.post(this.basePath + this.authPath, credentials, configUrlEncoded)
        .then((response) => {
            this.token = response.data.token
            console.log(this.token)
           
        })
        .catch(error => console.log(error)
        ) 

      },
      async clear() {
        this.token = ''
    }
    }
  })
