import { createRouter, createWebHistory } from 'vue-router'
import AppHome from '@/views/AppHome.vue'
import AufgabenSpielView from '@/views/AufgabenSpielView.vue'
import AufgabePageView from '@/views/AufgabePageView.vue'
import CsvReport from '@/views/CsvReport.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: AppHome
  },
  {
    path: '/aufgaben',
    name: 'AufgabenSpielView',
    component: AufgabenSpielView
  },
  {
    path: '/aufgaben/aufgabe/:id',
    name: 'AufgabePageView',
    component: AufgabePageView
  },
  {
    path: '/csvreport',
    name: 'CsvReport',
    component: CsvReport
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
