<template>
  <div class="hello">
  <div>
    <h1>Aufgabenspiel</h1>
  </div>
  
    <div class="listWrapper">
      <div class="easyTasks">
        <h3>Grundlegende Aufgaben <br>(1 Punkt) </h3>
        <ul v-if="tasks">
        <li  v-for="task in this.getEaysyTasks()" :key="task.id">
            <router-link :to="'/aufgaben/aufgabe/' + task.id">{{ task.title.rendered }} </router-link>
        </li>
      </ul>
      </div>
      <div class="hardTasks">
        <h3>Erweiterte Aufgaben <br>(4 Punkte)</h3>
         <ul v-if="tasks">
        <li  v-for="task in this.getHardTasks()" :key="task.id">
          <router-link :to="'/aufgaben/aufgabe/' + task.id">{{ task.title.rendered }} </router-link>
        
        </li>
      </ul>
      </div>

    </div>
  </div>
</template>

<script >


import { mapState } from 'pinia'
import { useTaskStore } from '../stores/taskStore.js'
import { storeToRefs } from 'pinia'


export default {
  name: "AufgabenSpiel",
  setup() {
 
    const { fetchTasks } = useTaskStore()
    const { tasks } = storeToRefs(useTaskStore())

    fetchTasks()

    return { tasks }
  },
  computed: {
    ...mapState(useTaskStore, ['getEaysyTasks']),
    ...mapState(useTaskStore, ['getHardTasks'])
  
  }
}

</script>

