import { useApplicationDataStore } from '../stores/applicationDataStore.js'
import { useRoute } from 'vue-router'
import { useCredentialStore } from '@/stores/credentialStore.js'
import restService from '@/services/restService.js'

// const delay = ms => new Promise(res => setTimeout(res, ms));

export default {
    name:"AufgabePage",
    data() {
        const timeData = this.appDataStore.time != null ? this.appDataStore.time : 10 * 60 * 1000 ;
        const timerRunning = true
        return {
            error: null,
            success: false,
            frage1: null,
            frage2: null,
            frage3: null,
            frage4: null,
            frage5: null,
            frage6: null,
            frage7: null,
            frage8: null,
            frage9: null,
            timerRunning: timerRunning,
            time: timeData
        }
    },
    setup() {
        //data
        const now = new Date();

        const route = useRoute()
        const { fetchTaskWithCheck } = useTaskStore()

        const { task } = storeToRefs(useTaskStore())
        const { checked } = storeToRefs(useTaskStore())        
        const { fetchQuestions } = useQuestionStore()
        const { questions } = storeToRefs(useQuestionStore())
        const appDataStore = useApplicationDataStore()
        const credentialStore = useCredentialStore()
        const taskStore = useTaskStore()

        fetchQuestions()
        fetchTaskWithCheck(route.params.id, appDataStore.userName, now, appDataStore.gamestart, credentialStore.token)
        

    return { questions, task, appDataStore, credentialStore, now, checked, taskStore }
   
  },
  mounted() {
    // console.log(this.appDataStore.userName)
    // console.log(this.task.title.rendered)
    // delay(2000)
    // this.taskStore.fetchCheck( this.task.title.rendered, this.appDataStore.userName )
    // console.log(this.checked)

    //populate data for opening a task
    // const currentNow = new Date()

    // delay(2000);
    // if (this.checked != false) {

       

    // }
   
    //register back button event listener
    window.onpopstate = function() {
        //populate data for opening a task
        const currentNow = new Date()
        const appDataStore = useApplicationDataStore()
        const credentialStore = useCredentialStore()
        const data = JSON.stringify({
                title: "Probe Out",
                // aufgabentitel: this.task.title.rendered, 
                // einfach: this.task.einfach, 
                zp_begonnen: currentNow.toLocaleTimeString(),
                zp_abgeschlossen: currentNow.toLocaleTimeString(), 
                zp_spielbeginn: appDataStore.gamestart.toLocaleTimeString(),
                nutzername: appDataStore.userName,
                status: "publish"
        })
        restService.createTestProbe(data, credentialStore.token)
    }

  },
  computed: {
    ...mapState(useQuestionStore, ['getEaysyQuestions']),
    ...mapState(useQuestionStore, ['getHardQuestions']),
    ...mapState(useCredentialStore, ['getToken'])
  }, 
  methods: {
        valdiateInputs() {
            const oneCorrect = this.task.antwort1 === this.frage1
            const twoorrect = this.task.antwort2 === this.frage2
            const threeCorrect = this.task.antwort3 === this.frage3
            const fourCorrect = this.task.antwort4 === (this.frage4 ? "1" : "0")
            const fiveCorrect = this.task.antwort5 === (this.frage5 ? "1" : "0")
            const six = this.task.antwort6 === (this.frage6 ? "1" : "0")
            const seven = this.task.antwort7 === (this.frage7 ? "1" : "0")
            const eight = this.task.antwort8 === (this.frage8 ? "1" : "0")
            const nine = this.task.antwort9 === (this.frage9 ? "1" : "0")


            // console.log(oneCorrect)
            // console.log(twoorrect)
            // console.log(threeCorrect)
            // console.log(fourCorrect)
            // console.log(fiveCorrect)
            // console.log(six)
            // console.log(seven)
            // console.log(eight)
            // console.log(nine)
            // console.log(this.frage5)
            // console.log(this.task.antwort5)

            if (oneCorrect && twoorrect && threeCorrect && fourCorrect && fiveCorrect && six && seven && nine && eight) {
                //congrats now - 
                this.success = true

                // befülle eine neue testprobe für eine aufgabe
                const currentNow = new Date()
                // console.log("time " + currentNow.getTime())
                // console.log("time " + currentNow.toLocaleTimeString())
                const data = JSON.stringify({
                        title: "Probe Fin",
                        aufgabentitel: this.task.title.rendered, 
                        einfach: this.task.einfach, 
                        zp_begonnen: this.now.toLocaleTimeString(),
                        zp_abgeschlossen: currentNow.toLocaleTimeString(), 
                        zp_spielbeginn: this.appDataStore.gamestart.toLocaleTimeString(),
                        nutzername: this.appDataStore.userName,
                        status: "publish",
                        probeart: "Probe Fin"
                })
                // console.log( this.credentialStore.token )
                restService.createTestProbe(data, this.credentialStore.token)
      
            } else {
                //show error
                this.error = true
            }
        },
        endTimer() {
            this.timerRunning = false

            //push game start
            const data = JSON.stringify({
                            title: "Probe End",
                            nutzername: this.appDataStore.userName,
                            status: "publish"
                    })
            // console.log(  this.credentialStore.token )
            restService.createTestProbe(data, this.credentialStore.token)

            this.credentialStore.clear()
            this.appDataStore.clear()
        },
        updateTime(data) {
            // console.log(data.totalMilliseconds)
            // console.log(this.time)
            this.appDataStore.time = data.totalMilliseconds
        },
        startTimer() {
            this.timerRunning = true

        }
      

  }
}
</script>

