import { defineStore } from 'pinia'
import axios from 'axios'


export const useSettingsStore = defineStore({
    id: 'settings',
    state: () => ({
      textStartPage: "",
      textAnonymName: "",
      videoLink: "",
      videoText: "",
      pdfLink: "",
      rawData: null,
      loading: false,
      error: null,
      basePath: "https://testspiel.danielglueck.de/wp-json",
      taskPath: "/wp/v2/einstellungen"
    }),
    getters: {
     
    }, 
    actions: {
     
        async fetchSettings() {
            this.loading = true
            this.rawData = null 

            this.rawData = await axios.get(this.basePath + this.taskPath).then((response) => {

                console.log(response.data)
                this.textAnonymName = response.data[0].textanonymousname
                this.textStartPage = response.data[0].textstartpage
                this.videoLink = response.data[0].videolink
                this.videoText = response.data[0].videotext
                this.pdfLink = response.data[0].pdflink
              
                console.log(this.videoLink)
                return response.data
            }).catch(
                error => console.log(error)
            ).finally(
                () => { 
                    this.loading = false
                }
            )
        }
    }
  })
  